import React from 'react'
import styled from 'styled-components'
import ScrollAnim from 'rc-scroll-anim'
import WordCloudChart from 'components/Charts/WordCloudChart'
const ScrollParallax = ScrollAnim.Parallax

const StyledSolidBg = styled.div`
  height: 700px;
  position: absolute;
  background-color: #f9f9f9;
  width: 100%;
  transform: skewy(-10deg) translate3d(0, -18%, 0);
  z-index: -1;
`

const StyledWordDumy = styled.div`
  width: 320px;
  height: 320px;
  margin: 0 auto;
  background-color: #414141;
`

const Tech = () => (
  <div>
    <StyledSolidBg />
    <ScrollParallax
      animation={{ opacity: 1 }}
      style={{ opacity: 0, paddingTop: 60 }}
    >
      <WordCloudChart />
    </ScrollParallax>
    <ScrollParallax
      animation={{ x: 0 }}
      style={{ transform: 'translateX(-100px)', margin: '10px auto' }}
    >
      <StyledWordDumy>word cloud area</StyledWordDumy>
    </ScrollParallax>
    <ScrollParallax
      animation={{ scale: 1 }}
      style={{ transform: 'scale(0)', margin: '10px auto' }}
    >
      <StyledWordDumy>word cloud area</StyledWordDumy>
    </ScrollParallax>
  </div>
)

export default Tech



